.Banner-container {
  background-size: cover;
  background-position: center center;
  object-fit: contain;
  height: 448px;

  padding: 20px;
  padding-top: 200px;
}
.banner_desc {
  font-size: 20px;
  font-weight: 400;
  width: 40%;
}

.button_container {
  display: flex;
}
.button {
  width: 100px;
  transition: all ease 0.5s;
  font-size: 25px;
  border: solid;
  text-align: center;
  margin: 10px;
  border-radius: 10%;
  padding: 5px 0px;
  cursor: pointer;
}
.button:hover {
  width: 120px;
}
.banner_title {
  font-size: 70px;
}
