.row_poster {
  width: 120px;
  object-fit: cover;
  transition: all ease 0.5s;
  cursor: pointer;
}
.row_poster:hover {
  width: 150px;
}
.row_posters {
  display: flex;
}
.row_poster-container {
  padding: 5px;
}
.row-heading {
  text-align: left;
  margin-left: 5px;
}
.row {
  overflow: hidden;
}
.dont-show {
  display: none;
}
.show {
  display: block;
  background: rgba(0, 0, 0, 0.359);
  transform: translate(0px, -100px);
  margin-top: -100px;
  height: 100vh;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 30%;

  width: 100%;
  height: 100vh;
}
.close-container {
  transform: translate(-180px, -195px);
}
.close {
  font-size: 25px;
  background: transparent;
  color: white;
  cursor: pointer;
  border: none;
}
