@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&family=Roboto:wght@300&display=swap");
.movie-container {
  font-size: 10px;
  background: rgb(17, 17, 18);
  width: 75%;
  margin: auto;
  display: fixed;
  height: 75vh;
  overflow: hidden;
}
.image-container {
  background-size: cover;
  object-fit: cover;
  height: 200px;
  width: 500px;
  border-radius: 5%;
}
.header-container {
  display: flex;
}
.movie-name {
  padding-left: 10px;
  font-size: 50px;
  display: 1;
}
.movie-desc {
  padding: 40px;
  font-size: 20px;
  font-family: "Poppins";
  width: 600px;
  margin: auto;
  padding-bottom: 20px;
}
